#taggbox_main *,
#taggbox_main *::before,
#taggbox_main *::after {
  box-sizing: border-box;
}

.taggbox{
  overflow: auto;
}

.tb_app_container *,
.tb_app_container *::before,
.tb_app_container *::after {
  box-sizing: border-box;
}

.tb_app_container{
  width: 100%;
}

.tb_app_container a{
  text-decoration: none !important;
  color: #2b2b2b;
}

.alert-enter {
  opacity: 0;
  transform: scale(0.9);
}
.alert-enter-active {
  opacity: 1;
  transform: translateX(0);
  transition: opacity 300ms, transform 300ms;
}
.alert-exit {
  opacity: 1;
}
.alert-exit-active {
  opacity: 0;
  transform: scale(0.9);
  transition: opacity 300ms, transform 300ms;
}
.tb_text_hashtag {
    display: inline;
    //color: inherit !important;
}
.clearfix:after{
  content: '';
  display: table;
  clear: both;
}

.tb_app_container [tabindex="-1"]:focus, .tb_app_container [tabindex="-1"]:hover, .tb_app_container [tabindex="-1"]:active {
  outline: 0;
  box-shadow: none;
}
.tickerSubtitleView, .tickerTitleView{
  display: flex;
  justify-content: center;
  align-items: center;
}
.tickerTitleView {
  text-align: center;
  float: left;
  position: relative;
  padding: 0 20px;
}
.tickerSubtitleView h2, .tickerTitleView h1{
  margin: 0px;
}
.headerTicker marquee {
  width: 100%;
  white-space: nowrap;
}

.post-card-outer{
  line-height: 1.5;
}

.noPostContent.hide_post_strip {
  background-color: #5e2c2c;
  bottom: 0;
  left: 0;
  max-width: 100%;
  position: absolute;
  right: 0;
  width: 100%;
  z-index: 94;
}
.noPostContent.hide_post_strip .noPostMsgBlock {
  background-color: #1a2029;
  border-radius: 0;
  box-shadow: none;
  left: 0;
  margin: 0 auto;
  max-width: 100%;
  padding: 10px 0;
  position: relative;
  right: 0;
  top: auto;
  transform: none;
  -webkit-transform: none;
  -moz-transform: none;
  -ms-transform: none;
  -o-transform: none;
  width: 100%;
  z-index: 9;
}
.hide_post_strip .noPostMsgBlock p {
  color: #fff;
  margin: 10px;
  text-align: center;
  font-size: 12px;
}

