#reels_69 .flying-emojis .emoji.wiggle-1,
.theme65 .flying-emojis .emoji.wiggle-1 {
  animation: emerge 3s forwards, wiggle-1 1s ease-in-out infinite alternate;
}


@keyframes emerge {
  to {
      bottom: 85%;
      opacity: 0;
  }
}

@keyframes wiggle-1 {
  from {
      margin-left: -20px;
  }
  to {
      margin-left: 50px;
  }
}


#motion_71 .flying-emojis .emoji.wiggle-1,
#reels_73 .flying-emojis .emoji.wiggle-1 {
  animation: emerge_2 3s forwards, wiggle-2 1s ease-in-out infinite alternate;
}


@keyframes emerge_2 {
  to {
      bottom: 85%;
      opacity: 0;
  }
}

@keyframes wiggle-2 {
  from {
      margin-left: -20px;
  }
  to {
      margin-left: -50px;
  }
}

.interactive-animation{
  position: fixed;
  bottom: 0px;
  right: 0px;
  z-index: 1;
}
.interactive-animation .flying-emojis {
  position: fixed;
  top: 0px;
  bottom: 20px;
  left: 0px;
  right: 20px;
  overflow: hidden;
  pointer-events: none;
  user-select: none;
  z-index: 1;
}

.interactive-animation .flying-emojis .emoji {
  position: absolute;
  right: 20px;
  bottom: 20px;
}

.interactive-animation .flying-emojis .emoji.vapour-wiggle {
  animation: vapour 2.8s forwards, vapourWiggle 2.8s ease-in-out infinite alternate;
}
.interactive-animation .flying-emojis .emoji2.vapour-wiggle {
  animation: vapourLeft 3s forwards, vapourWiggleLeft 1s linear infinite;
}

.like-button{
  position: absolute;
  right: 30px;
  bottom: 10px;
  filter: drop-shadow(5px 5px 5px #cccccc38);
  z-index: 2;
}
.like-button.scaleup svg{
  animation: beat 1s infinite alternate;
	transform-origin: center;
}
@keyframes beat{
	to { transform: scale(1.2); }
}

@keyframes vapour {
  to {
    bottom: 85%;
    opacity: 0;
    filter: blur(5px);
  }
}

@keyframes vapourWiggle {
  0% {
    margin-right: 0px;
  }
  50%{
    margin-right: 20px;
  }
  100%{
    margin-right: 30px;
  }
}

@keyframes vapourLeft {
  to {
    bottom: 85%;
    opacity: 0;
    filter: blur(5px);
  }
}

@keyframes vapourWiggleLeft {
  0% {
    margin-left: 0px;
  }
  50%{
    margin-left: 20px;
  }
  100%{
    margin-left: 30px;
  }
}

