.tb_ss_media_wrap{
    position: relative;
    display: block;
    width: 100%;
    height: 100%;
    overflow: hidden;
}
.tb_ss_media_wrap img{
    height: 100%;
}

.tb_ss_blur_bg, .tb_ss_blur_bg_inner{
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    z-index: 0;
}

.tb_ss_blur_bg:before, .tb_ss_blur_bg_inner:before{
    position: absolute;
    content: "";
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
}

.tb_ss_blur_bg:before{
    background: rgba(0,0,0,.5);
    -webkit-backdrop-filter: blur(12px);
    backdrop-filter: blur(12px);
}

.tb_ss_blur_bg_inner:before{
    background: rgba(0,0,0,.3);
    -webkit-backdrop-filter: blur(12px);
    backdrop-filter: blur(10px);
}

.tb_ss_bg_color{
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    z-index: 0;
    background-color: #545454;
}

.tb_ss_post_article{
    position: relative;
    z-index: 1;
    width: 85%;
    margin: 0 auto;
    padding: 2% 0;
    height: 100vh;
}

.tb_ss_post_article_full{
    position: relative;
    z-index: 1;
    width: 100%;
    margin: 0 auto;
    padding: 0;
    height: 100%;
}

