.tb_ss_image {
    height: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    position: relative;
    z-index: 2;
    transition: 8s all ease-in-out;
}

.is-visible .tb_ss_post_animated_media .tb_ss_image{
    -webkit-transform: scale(1.45);
    transform: scale(1.45);
    transition-delay: 2s;
}

