.tb_ss_author_wrapper, .tb_ss_social_, .tb_ss_content, .tb_ss_author_profile{
    position: relative;
}

.is-visible .tb_ss_author_wrapper, .is-visible .tb_ss_social_, .is-visible .tb_ss_content, .is-visible .tb_ss_author_profile{
    -webkit-animation: tb_ss_text_animation 1s 1 cubic-bezier(.165,.84,.44,1) .1s;
    -moz-animation: tb_ss_text_animation 1s 1 cubic-bezier(.165,.84,.44,1) .1s;
    -o-animation: tb_ss_text_animation 1s 1 cubic-bezier(.165,.84,.44,1) .1s;
    animation: tb_ss_text_animation 1s 1 cubic-bezier(.165,.84,.44,1) .1s;
    -webkit-animation-fill-mode: both;
    -moz-animation-fill-mode: both;
    -ms-animation-fill-mode: both;
    -o-animation-fill-mode: both;
    animation-fill-mode: both;
    opacity: 1;
    filter: alpha(opacity=100);
    -webkit-transform: translateZ(0);
}

.is-visible .tb_ss_content{
    animation-delay: .3s;
    -webkit-animation-delay: .3s;
}

.is-visible .tb_ss_author_wrapper{
    animation-delay: .6s;
    -webkit-animation-delay: .6s;
}



@-webkit-keyframes tb_ss_text_animation{
    0%{
        top:70px;
        opacity:0
    }
    100%,50%{
        top:0;
        opacity:1
    }
}
@-moz-keyframes tb_ss_text_animation{
    0%{
        top:70px;
        opacity:0
    }
    100%,50%{
        top:0;
        opacity:1
    }
}
@-o-keyframes tb_ss_text_animation{
    0%{
        top:70px;
        opacity:0
    }
    100%,50%{
        top:0;
        opacity:1
    }
}
@keyframes tb_ss_text_animation{
    0%{
        top:70px;
        opacity:0
    }
    100%,50%{
        top:0;
        opacity:1
    }
}



.tb_ss_top_layers, .tb_ss_bottom_layers{
    z-index: 3;
	position: absolute;
    height: 100%;
    width: 100%;
}

.tb_ss_slide_layers {
	height: 12.52%;
	background: #2196f3;
	opacity: 0;
	position: absolute;
	width: 0
}

.tb_ss_top_layers .tb_ss_slide_layers {
	left: 0;
    right: auto;
}

.tb_ss_bottom_layers .tb_ss_slide_layers {
	right: 0;
    left: auto;
}

.tb_ss_top_layers .tb_ss_slide_layers:nth-child(1){
	top: 0
}
.tb_ss_top_layers .tb_ss_slide_layers:nth-child(2){
	top: 12.5%
}
.tb_ss_top_layers .tb_ss_slide_layers:nth-child(3){
	top: 25%
}
.tb_ss_top_layers .tb_ss_slide_layers:nth-child(4){
	top: 37.5%
}

.tb_ss_bottom_layers .tb_ss_slide_layers:nth-child(1){
	bottom: 0
}
.tb_ss_bottom_layers .tb_ss_slide_layers:nth-child(2){
	bottom: 12.5%
}
.tb_ss_bottom_layers .tb_ss_slide_layers:nth-child(3){
	bottom: 25%
}
.tb_ss_bottom_layers .tb_ss_slide_layers:nth-child(4){
	bottom: 37.5%
}

.is-visible .tb_ss_slide_layers:nth-child(1){
	animation-delay: .2s;
    -webkit-animation-delay: .2s;
}

.is-visible .tb_ss_slide_layers:nth-child(2){
	animation-delay: .4s;
    -webkit-animation-delay: .4s;
}

.is-visible .tb_ss_slide_layers:nth-child(3){
	animation-delay: .6s;
    -webkit-animation-delay: .6s;
}

.is-visible .tb_ss_slide_layers:nth-child(4){
	animation-delay: .8s;
    -webkit-animation-delay: .8s;
}
.is-visible .tb_ss_slide_layers{
    animation: tb_ss_overlay_slides 2s;
}

.tb_ss_rating__ .tb__rating__{
    padding: 0px;
}
.tb_ss_rating__ .tb_rating_ico__{
    font-size: calc(0.3vw + 0.3vh + 8px);
}


@keyframes tb_ss_overlay_slides{
    0%{
        width: 0;
        opacity:0;
    }
    40%{
        width: 100%;
        opacity:1;
    }
    60%{
        width: 100%;
        opacity:1;
    }
    100%{
        width: 0;
        opacity:0;
    }
}

