.tb_ap_post_container{
    display: flex;
    flex-wrap: wrap;
    width: 100%;
}

.tb_ap_post_wrapper{
    width: 16.66%;
    padding: 5px;
}

.tb_ap_image {
    padding-bottom: 100%;
}

.tb_ap_post_in{
    display: block;
    position: relative;
    cursor: pointer;
}

.tb_ap_post_media_wrapp{
    position: relative;
    overflow: hidden;
}

.tb_ap_post_details, .tb_ap_post_overlay {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 10px;
    opacity: 0;
    visibility: hidden;
}
.tb_ap_post_overlay {
    z-index: 1;
}

.tb_ap_post_details {
    z-index: 2;
}

.tb_ap_post_in:hover .tb_ap_post_details {
    opacity: 1;
    visibility: visible;
}

.tb_ap_post_in:hover .tb_ap_post_overlay {
    opacity: 0.8;
    visibility: visible;
}

.tb_ap_contant_wrapper {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    margin: 0 auto;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 15px;
}

.tb_ap_image {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    background-color: rgba(255,255,255,.2);
}

.tb_ap_author {
    width: calc(100% - 30px);
}

.tb_ap_author_profile {
    height: 44px;
    width: 44px;
    border-radius: 50%;
    margin-right: 10px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    display: flex;
    flex-shrink: 0;
    background-color: rgba(0,0,0,.2);
    overflow: hidden;
}

.tb_ap_authorname {
    font-family: 'Inter', sans-serif;
    font-size: 14px;
    font-weight: 600;
    margin-bottom: 4px;
    color: #ffffff;
    white-space: nowrap;
    display: block;
    overflow: hidden;
    text-overflow: ellipsis;
}

.tb_ap_author {
    display: flex;
    align-items: center;
    flex-grow: 1;
}

.tb_ap_author_wrapper {
    display: flex;
    align-items: center;
    position: absolute;
    width: 100%;
    bottom: 0;
    left: 0;
    padding: 10px;
}

.tb_ap_author_info {
    overflow: hidden;
    display: flex;
    flex-shrink: 0;
    flex-direction: column;
    width: calc(100% - 54px);
    padding-right: 10px;
}

.tb_ap_post_info {
    display: flex;
    align-items: center;
}

.tb_ap_seprator {
    height: 4px;
    width: 4px;
    min-width: 4px;
    background-color: #ffffff;
    border-radius: 50%;
    margin: 0 6px;
    opacity: 0.8;
    display: flex;
    flex-shrink: 1;
}

.tb_ap_username, .tb_ap_time {
    font-family: 'Inter', sans-serif;
    font-size: 10px;
    color: #ffffff;
    font-weight: 500;
    opacity: 0.8;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

.tb_ap_username{
    max-width: 65%;
}

.tb_ap_time{
    max-width: 35%;
}

.tb_ap_social_ {
    font-size: 24px;
    color:#fff;
}

.tb_ap_content {
    font-size: 14px;
    color: #fff;
    word-break: break-word;
    text-align: center;
    white-space: pre-line;
    overflow: hidden;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 4;
    text-overflow: ellipsis;
    width: 100%;
}

.tb_ap_content .tb_text_links_{
    color: inherit !important;
    text-decoration: underline !important;
    display: inline;
}

.tb_ap_post_in:hover .tb_share_wrapper{
    opacity: 1;
    visibility: visible;
}


.tb_ap_video_ico {
    position: absolute;
    right: 12px;
    top: 12px;
    z-index: 2;
    color: #fff;
    font-size: 12px;
    filter: drop-shadow(2px 2px 2px rgba(0,0,0,.4));
}

.tb_ap_media_wrap img{
    width: 100%;
    height: 290px;
    object-fit: cover;
}

.tb_ap_multi_images_ico {
    position: absolute;
    right: 12px;
    top: 12px;
    color: #fff;
    font-size: 17px;
    z-index: 2;
    filter: drop-shadow(2px 2px 2px rgba(0,0,0,.4));
}

.tb_ap_multi_images_ico_bottom {
    position: absolute;
    right: 12px;
    bottom: 12px;
    color: #fff;
    font-size: 17px;
    filter: drop-shadow(2px 2px 2px rgba(0,0,0,.4));
}

.tb_ap_youtube_ico {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    margin: 0 auto;
    text-align: center;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 34px;
}

// .themeSquare .theme66 .tb_mwf_contant_wrapper{
//     min-height: 115px;
// }

.tb_mwf_contant_wrapper .tb-cTBfont- div,
.tb_fc_content_wrapper__ .tb-cTBfont- div{
    word-wrap:break-word;
    display: block;
    -webkit-box-orient: vertical;
    overflow: hidden;
}

#squre_66.tb_ap_post_container.display,
#squre_67.tb_ap_post_container.display,
#reels_69.tb_ap_post_container.display{
    height: 100vh;
    overflow: hidden;
}

#squre_66 .tb_ap_post_details, #squre_66 .tb_ap_post_overlay, #squre_66 .tb_ap_contant_wrapper,
#squre_66 .tb_ap_author_wrapper{
    position: inherit;
}
#squre_66 .tb_ap_post_details{
    opacity: 1;
    visibility: visible;
    padding: 0px;
    margin-top: 10px;
}
#squre_66 .tb_ap_author_wrapper{
    padding: 0px;
}
#squre_66 .tb_ap_contant_wrapper{
    padding: 0px;
    margin-top: 10px;
}
#squre_66 .tb_ap_contant_wrapper .tb_ap_content{
    text-align: left;
}
#squre_66 .tb_ap_post_wrapper{
    padding: 5px;
}
#squre_66 .tb_ap_post_in{
    box-shadow: 0px 0px 10px #ccc;
    padding: 10px;
    border-radius: 10px;
    height: 100%;
}
#squre_66.embed .tb_ap_post_overlay{
    opacity: 1;
    visibility: visible;
}
#squre_66.embed .tb_ap_post_small{
    display: flex;
    flex-wrap: wrap;
    width: 100%;
}
#squre_66.embed .tb_mwf_contant_wrapper{
    padding-top: 0px;
}
#squre_66.embed .tb_ap_media_wrap{
    height: 350px;
}
#squre_67 .tb_ap_social_{
    padding: 8px;
    background-color: #00000017;
    border-radius: 50%;
    font-size: 18px;
}


#reels_69 .tb_ap_post_overlay{
    opacity: 1;
    visibility: visible;
    bottom: 0px;
    top: inherit;
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 1) 100%) !important;
}
#reels_69 .tb_ap_post_overlay .tb_mwf_contant_wrapper{
    display: none;
}
#reels_69 .tb_ap_post_overlay .tb_mwf_author_wrapper{
    margin-bottom: 0px;
}
#reels_69 .tb_ap_media_wrap{
    height: 500px;
}
#reels_69 .tb_ap_media_wrap img{
    height: 100%;
}

#reels_69 .tb_ap_post_media_wrapp{
    position: relative;
    border-radius: 15px;
    overflow: hidden;
}
#reels_69.embed .tb_ap_post_small{
    display: flex;
    flex-wrap: wrap;
}
#reels_69.embed .tb_ap_post_wrapper .tb_ap_post_in {
    padding: 0px !important;
}
#reels_69.embed .tb_ap_post_wrapper{
    height: 50vh;
}
#reels_69.embed .tb_ap_post_wrapper .tb_ap_post_in,
#reels_69.embed .tb_ap_post_media_wrapp,
#reels_69.embed .tb_ap_post_media_wrapp .tb_fc_media_wrap{
    height: 100%;
}
#reels_69 .tb_mwf_author_wrapper .tb_mwf_authorname{
 color: #fff !important;
}
#reels_69 .tb_mwf_author_wrapper .tb_mwf_post_info>div {
    color: #fff !important;
    font-size: 11px;
}
#reels_69 .tb_mwf_author_wrapper .tb_mwf_post_info .tb_mwf_seprator{
    background-color: #fff !important;
}

@media screen and (max-width: 1200px) {
    #reels_69.embed .tb_ap_post_wrapper{
        width: 25%;
    }
}
@media screen and (max-width: 992px) {
    #reels_69.embed .tb_ap_post_wrapper{
        width: 33.33%;
    }
}
@media screen and (max-width: 767px) {
    #reels_69.embed .tb_ap_post_wrapper{
        height: auto;
    }
    #reels_69.embed .tb_ap_post_wrapper{
        width: 50%;
    }
}

@media screen and (max-width: 576px) {
    #reels_69.embed .tb_ap_post_wrapper{
        width: 100%;
    }
}